<template>
<section>
     <div class="header"></div>
     <el-form :label-position="labelPosition" label-width="80px"  class="mar-top">
            <el-form-item label="">
                <el-select v-model="formData.type" placeholder="">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="formData.taskSite" placeholder=""  clearable></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="formData.endSite" placeholder=""  clearable></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="formData.description" placeholder=""  clearable></el-input>
            </el-form-item>

            <el-form-item label="()">
                  <el-input-number v-model="formData.tips" :min="1" :max="20"></el-input-number>
            </el-form-item>
             <el-form-item label="">
                <el-select v-model="formData.limitDate" placeholder="">
                    <el-option
                    v-for="item in timeList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item style="text-align:right">
                <el-button type="primary" icon="el-icon-upload" @click="submit" ></el-button>
            </el-form-item>

    </el-form>
</section>
 
</template>
<script>
const URL = {
    ORDERADD:'/order/add',
    SESSION:'/user/getSession'
}
export default {
  data(){
      return {
        imageUrl: '',
        labelPosition:'left',
        choosed:true,
        formData:{
            tips:'',
            limitDate:'',
            description:'',
            type:'',
            taskSite:'', //label 
            endSite:''
        },
        options: [{
          value: 1000,
          label: ''
        }, {
          value: 2000,
          label: ''
        }, {
          value: 3000,
          label: ''
        }],
        timeList: [{
          value: 0.25,
          label: '15'
        }, {
          value: 0.5,
          label: ''
        }, {
          value: 1,
          label: '1'
        },
        {
          value: 3,
          label: '2'
        },
         {
          value: 6,
          label: '6'
        },
        {
          value: 24,
          label: ''
        }
        ]
      };
  },
  methods:{
      submit(){
          let that = this;
          that.formData.limitDate = Date.now()+1000*60*60*that.formData.limitDate;
          that.formData.consignmentId = sessionStorage.getItem('zhkuTip_id');
          if(that.formData.taskSite.length === 0 && that.formData.taskSite.length>=15){
              that.$message.error('15')
              return
          }
          if(that.formData.endSite.length === 0 && that.formData.endSite.length>=15){
              that.$message.error('15')
              return
          }
          if(that.formData.description.length === 0 && that.formData.description.length>=25){
              that.$message.error('25')
              return
          }
          that.$axios.post(URL.ORDERADD,qs.stringify(that.formData))
          .then(res=>{
              console.log(res);
              if(res.data.status === 1){
                  that.$router.push({
                      name:'index'
                  })
              }
          })
      }
  },
  created(){
      this.$axios.get(URL.SESSION)
      .then(res=>{
          if(res.data.status!==1){
              this.$router.push({
                  name:'login'
              })
          }
      })
  }
}
</script>
<style lang="less" scoped>
    section{
        .header{
            padding: 0.5rem;
            text-align: center;
            background: #4DCADA;
            color:#fff;
            font-size: 600;
        }
        .mar-top{
            margin-top: .6rem;
        }
        .el-form{
            padding: 0rem 1rem;
        }
    }
    
</style>




